<template>
  <div class="zhishiku" :class="$root.themeHomeChange === '1' ? 'zhishikuLight' : ''" >
    <!-- <div class="manageBtn" background-color="#00AAFF">
      <bda-button @click="goBack">返回</bda-button>
    </div> -->
    <div class="zhishikuTable">
      <div class="leftMenu">
        <el-menu
          default-active="2-1-1"
          class="el-menu-vertical"
          :text-color="$root.themeHomeChange === '1' ? '#fff' : '#fff'"
          @select="handleSelect"
          unique-opened
        >
          <div v-for="item in menuList" :key="item.index">
            <el-menu-item v-if="item.children.length === 0" :index="item.index">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </el-menu-item>
            <el-submenu v-else index="2">
              <template slot="title">
                <i :class="item.icon"></i>
                <span slot="title">{{ item.title }}</span>
              </template>
              <el-menu-item-group>
                <div v-for="item1 in item.children" :key="item1.index">
                  <el-menu-item
                    v-if="item1.children.length === 0"
                    :index="item1.index"
                  >
                    <i :class="item1.icon"></i>
                    <span slot="title">{{ item1.title }}</span>
                  </el-menu-item>
                  <el-submenu v-else index="2">
                    <template slot="title">
                      <i :class="item1.icon"></i>
                      <span slot="title">{{ item1.title }}</span>
                    </template>
                    <el-menu-item-group>
                      <div v-for="item2 in item1.children" :key="item2.index">
                        <el-menu-item
                          v-if="item2.children.length === 0"
                          :index="item2.index"
                        >
                          <i :class="item2.icon"></i>
                          <span slot="title">{{ item2.title }}</span>
                        </el-menu-item>
                      </div>
                    </el-menu-item-group>
                  </el-submenu>
                </div>
              </el-menu-item-group>
            </el-submenu>
          </div>
        </el-menu>
      </div>
      <div class="rightTable" ref="companyCenterMain">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  import Interface from "../../../base/mixin/enterprise-manage-center";
export default {
  props: ["routerList"],
  mixins: [Interface],
  data() {
    return {
      routerList1: [],
      menuList: [
        {
          title: "企管中心",
          path: "/regulation",
          index: "2-1",
          icon: "el-icon-folder-opened",
          children: [
            {
              title: "法律法规",
              path: "/regulationTable",
              index: "2-1-1",
              icon: "",
              children: [],
            },
            {
              title: "制度规范",
              path: "/regulationTable",
              icon: "",
              index: "2-1-2",
              children: [],
            },
            {
              title: "内控管理规则",
              path: "/regulationTable",
              icon: "",
              index: "2-1-3",
              children: [],
            },
            {
              title: "其他",
              path: "/regulationTable",
              icon: "",
              index: "2-1-4",
              children: [],
            },
          ],
        },
      ],
    };
  },
  mounted() {
    this.getManageTypeArray()
  },
  created() {
    if (this.$route.path === "/companyCenter") {
      this.init(this.$route.query.itemIndex);
    }
  },
  watch: {
    $route(v) {
      if (v.path === "/companyCenter") {
        this.init(this.$route.query.itemIndex);
      }
    },
  },
  methods: {
    //获取类型树
    getManageTypeArray() {
      this.manageTypeArray({}).then((res) => {
        if (res) {
          let array = []
          array = res.map(v => {
            return {
              title: v.name,
              index: v.code,
              icon: "",
              children: [],
            }
          })
          this.menuList = [
            {
              title: "企管中心",
              path: "/regulation",
              index: "2-1",
              icon: "el-icon-folder-opened",
              children: array
            }
          ]
          sessionStorage.setItem("departmentList", JSON.stringify(res));
        }
      }).finally(() => {
      })
    },

    goBack() {
      this.routerList.forEach((el, key) => {
        if (el == "/companyCenterMain") {
          this.$emit("handleSelect", key);
        }
      });
    },

    init(index) {
      index = index ? index : '2-2-1'
      this.setrouterList1(this.menuList);
      this.$router.push({
        path: this.routerList1[index],
        query: {
          ...this.$route.query,
          index: this.$route.query.index,
          itemIndex: index,
          actNav: this.$route.query.actNav,
        },
      });
    },
    // 设置回调函数，取出树状数据中的所需数据，扁平化，减少后续循环
    setrouterList1(data) {
      data.forEach((item) => {
        if (item.children.length === 0) {
          this.routerList1[item.index] = item.path;
        } else {
          this.setrouterList1(item.children);
        }
      });
    },
    handleSelect(key) {
      // console.log(key, keyPath, this.routerList1[key]);
      // 获取设置的路由地址
      this.$router.push({
        path: this.routerList1[key],
        query: {
          ...this.$route.query,
          index: this.$route.query.index,
          itemIndex: key,
          actNav: this.$route.query.actNav,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.zhishiku {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  /deep/ {
    /*修改按钮样式*/
    // .manageBtn {
    //   margin-bottom: 20px;
    // }
    .el-button {
      background-color: #041338;
      border: 1px solid #4389f9;
      border-radius: 4px;
      color: #fff;
    }

    .zhishikuTable {
      width: 100%;
      // height: calc(100% - 52px);
      height: 100%;
      display: flex;

      .leftMenu {
        width: 200px;
        height: 100%;
        margin-right: 20px;
        background: #1a3066;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
        .el-menu-vertical:not(.el-menu--collapse) {
          min-width: 150px;
          font-size: 14px;
          font-weight: bold;
        }

        .el-menu {
          background: #1a3066;
          border: none;
          .el-menu-item,
          .el-submenu__title {
            &:hover {
              color: #fff;
              background: rgba(#4389f9, 0.3);
            }
          }
          .el-menu-item.is-active {
            color: #fff;
            background: #4389f9;
          }
        }
      }

      .rightTable {
        width: calc(100% - 221px);
      }
    }
  }
}

.zhishikuLight{
  /deep/.el-button {
    background-color: #4389f9;
    border: 1px solid #4389f9;
    border-radius: 4px;
    color: #fff;
  }
  /deep/.el-input .el-input__inner{
    background: #ffffff;
  }
  .zhishikuTable {
    .leftMenu {
      background: #ffffff;
      .el-menu {
        border: none;
        background: #ffffff;
        .el-menu-item-group{
          background: #ffffff;
        }
        .el-menu-item,
        .el-submenu__title {
          background: #ffffff;
          >span{
            color: #717171;
          }
          &:hover{
            color: #ffffff!important;
            background: rgba(#4389F9, 0.9)!important;
            span{
              color: #ffffff!important;
            }
          }
        }
        .el-menu-item.is-active el-submenu__title{
          color: #de4040;
          background: #ffffff;
        }
      }
    }

  }
}
</style>